*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #674baf;
  background-image: url("../images/bg-mobile.svg");
  background-repeat: no-repeat;
  background-size: contain;
  padding: 10px 10px;
}
@media only screen and (min-width: 375px) {
  body {
    padding: 30px 20px;
  }
}
@media only screen and (min-width: 620px) {
  body {
    padding: 50px 35px;
  }
}
@media only screen and (min-width: 1350px) {
  body {
    background-image: url("../images/bg-desktop.svg");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 10px 40px;
  }
}

img {
  width: 100%;
}

.huddle {
  width: 200px;
  margin-bottom: 0;
}
@media only screen and (max-width: 375px) {
  .huddle {
    width: 150px;
  }
}
@media only screen and (max-width: 619px) {
  .huddle {
    height: 150px;
  }
}
@media only screen and (min-width: 620px) {
  .huddle {
    height: 100px;
  }
}
@media only screen and (min-width: 760px) {
  .huddle {
    margin-bottom: 120px;
  }
}
@media only screen and (min-width: 1350px) {
  .huddle {
    margin-bottom: 50px;
    height: 100px;
  }
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media only screen and (min-width: 1350px) {
  main {
    display: flex;
    flex-direction: row;
    gap: 35px;
  }
}

@media only screen and (min-width: 620px) {
  .main-img img {
    width: 500px;
  }
}
@media only screen and (min-width: 760px) {
  .main-img img {
    width: 600px;
  }
}
@media only screen and (min-width: 1000px) {
  .main-img img {
    width: 100%;
  }
}
@media only screen and (max-width: 375px) {
  .main-img {
    margin-bottom: 20px;
  }
}
@media only screen and (min-width: 375px) {
  .main-img {
    margin-bottom: 25px;
  }
}
@media only screen and (min-width: 620px) {
  .main-img {
    margin-bottom: 80px;
  }
}
@media only screen and (min-width: 1350px) {
  .main-img {
    margin-bottom: 0;
    flex: 1 1 300px;
  }
}

@media only screen and (min-width: 1350px) {
  .main-text {
    flex: 1 1 300px;
  }
}

button {
  border: none;
  color: #674baf;
  background-color: white;
  padding: 10px 70px;
  border-radius: 24px;
  box-shadow: 0px 5px 10px 5px rgba(0, 0, 0, 0.2);
  transition: all 0.15s ease;
  cursor: pointer;
}
@media only screen and (min-width: 375px) {
  button {
    margin-top: 25px;
    padding: 10px 90px;
  }
}

button:hover {
  color: white;
  background-color: #e882e8;
}

footer {
  text-align: center;
  margin: 50px;
}
footer i {
  color: white;
  border: 2px solid white;
  border-radius: 50%;
  padding: 10px;
  margin-left: 15px;
}
@media only screen and (min-width: 1350px) {
  footer i {
    font-size: 20px;
  }
}
footer .fa-facebook-f {
  margin-left: 0;
  border-radius: 50%;
  padding: 11px 13.2px;
}
@media only screen and (min-width: 1350px) {
  footer {
    text-align: end;
  }
}

i:hover {
  color: #e882e8;
  cursor: pointer;
  border-color: #e882e8;
}

body {
  font-size: 15px;
}

.main-text {
  text-align: center;
}
.main-text h1, .main-text button {
  font-family: "Poppins", sans-serif;
}
.main-text h1 {
  color: white;
  font-size: 26px;
}
@media only screen and (min-width: 375px) {
  .main-text h1 {
    width: 300px;
  }
}
@media only screen and (min-width: 450px) {
  .main-text h1 {
    font-size: 2em;
    margin: 0 auto;
    width: 370px;
  }
}
@media only screen and (min-width: 620px) {
  .main-text h1 {
    font-size: 3em;
    width: 540px;
  }
}
@media only screen and (min-width: 760px) {
  .main-text h1 {
    width: 100%;
  }
}
.main-text p {
  font-family: "Open Sans", sans-serif;
  color: white;
  margin-top: 15px;
  margin-bottom: 25px;
  line-height: 1.8;
}
@media only screen and (min-width: 375px) {
  .main-text p {
    width: 300px;
    margin: 0 auto;
  }
}
@media only screen and (min-width: 450px) {
  .main-text p {
    font-size: 1.25em;
    width: 370px;
    margin: 0 auto;
  }
}
@media only screen and (min-width: 620px) {
  .main-text p {
    margin-top: 25px;
    font-size: 1.5em;
    width: 450px;
  }
}
@media only screen and (min-width: 620px) {
  .main-text p {
    margin-top: 25px;
    font-size: 1.5em;
    width: 450px;
  }
}
@media only screen and (min-width: 760px) {
  .main-text p {
    width: 100%;
    margin-bottom: 40px;
  }
}
@media only screen and (min-width: 1350px) {
  .main-text p {
    font-size: 1.35em;
    margin-bottom: 0;
    margin-top: 0;
  }
}
@media only screen and (min-width: 450px) {
  .main-text button {
    font-size: 1.2em;
  }
}
@media only screen and (min-width: 1350px) {
  .main-text button {
    padding: 10px 70px;
  }
}
@media only screen and (min-width: 1350px) {
  .main-text {
    text-align: start;
  }
}